@import url('https://fonts.googleapis.com/css2?family=Forum&display=swap');
#navbar{
  background-color: black;
} 
a{
  text-decoration: none;
  color: white;
}

 /*_______ Navbar Starts Here _______*/
 
 .container,.icons-container{
   display: flex;
 }

 .container{
  width: 100vw;
  flex: 100%;
 }

 .icons-container{
  flex:50%;
  margin: 0 -15px;
 }
 .containerIcon-container{
  float: right;
  margin-right: 30px;
  margin-top: 30px;
 }
/* Logo Container */
 .logo{
  margin-left: 30px;
  margin-top: 30px;
  font-size: 16px;
  flex: 50%;
 }

/* Logo image tag */
#logo-img{
  height: 1.8em;
  width: auto;
}
/* select all img tag icons */
a>img{
  height: 1.8em;
  width: fit-content;
}
/* all flex li items */
.icons{
  margin: 0px 15px;
}

@media only screen and (max-width: 450px){
  #logo-img , a>img{
    height: 1.4em;
    width: auto;
  }
  .icons{
    margin: 0px 7.5px;
  }
}
/*_______ Navbar Ends Here _______*/

/* ///////////////////////////////// */

/*_______ Hero Component Starts Here_______*/

.containerHeroContainer{
  max-height: 100vh;
}

.heroContainer{
  padding: 90px;
  display: flex;
  flex-wrap: wrap;
  color: white;
}

.HeroHeadText{
  flex: 50%;
  font-size: 64px;
  font-weight: 600;
}
.HeroSubText{
  font-size: 40px;
  font-weight: 400;
}
#HeroBg{
  margin-top: -90px;
  margin-left: -20px;
  flex: 50%;
  height: 810px;
  width: 607.5px;
  object-fit: contain;
  z-index: -1 !important;
}

#work{
  position: absolute;
  top: 47.5%;
  width: 90px;
  text-align: center;
  font-family: 'Forum';
  font-size: 24px;
  background-color: rgb(255, 255, 255);
  transform: rotate(270deg);
}

#work>a,#contact>a{
  color: black;
}

#contact{
  position: absolute;
  top: 47.5%;
  width: 90px;
  text-align: center;
  right:0;
  font-family: 'Forum';
  font-size: 24px;
  background-color: rgb(255, 255, 255);
  transform: rotate(90deg);
}

#resume{
  position: absolute;
  bottom: 20px;
  font-size: 24px;
}

@media only screen and (max-width: 1350px) {
  .HeroHeadText , #HeroBg {
    flex: 100%;
  }
  .HeroHeadText{
    margin-left: 30px ;
  }
  #resume{
    visibility: hidden;
  }
  #HeroBg{
    margin-top: 0;
    margin-left: 0;
    height: 600px;
    width: 100%;
  }
  .heroContainer{
    padding: 0;
  }
  #work , #contact{
    border-radius: 20px;
    font-size: 20px;
    position: absolute;
    transform: rotate(0);
    top: 90%;
    height: max-content;
  }
  #work{
    left: 30px;
  } 
  #contact{
    right: 30px;
  }
}
@media only screen and (max-width: 450px){
    .HeroHeadText{
      font-size: 32px;
    }
    .HeroSubText{
      font-size: 24px;
    }
    #HeroBg{
      margin-top: -40px;
    }
}
/* _______Hero Component Ends Here _______ */

