#Work{
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
}

#Work h1{
  color: white;
  padding: calc(50vh - 50px) 0;
  margin: 0;
  width: 100vw;
  height: fit-content;
  text-align: center;
  font-size: 4rem;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
}